import { withBase } from "@vuepress/client";
import { isString } from "@vuepress/shared";
import {
  type VNode,
  defineComponent,
  h,
  onMounted,
  onUnmounted,
  ref,
} from "vue";
import { useRoute } from "vue-router";

import { EyeIcon, FireIcon } from "@theme-hope/modules/info/components/icons";
import { useMetaLocale } from "@theme-hope/modules/info/composables/index";

export default defineComponent({
  name: "PageViewInfo",

  inheritAttrs: false,

  props: {
    /**
     * Whether show pageview and it's path
     *
     * ÊòØÂê¶ÊòæÁ§∫ÊµèËßàÈáè‰ª•ÂèäÂÖ∂Ë∑ØÂæÑ
     */
    pageview: {
      type: [Boolean, String],
      default: false,
    },

    /**
     * Whether in pure mode
     *
     * ÊòØÂê¶Â§Ñ‰∫éÁ∫ØÂáÄÊ®°Âºè
     */
    pure: Boolean,
  },

  setup(props) {
    const route = useRoute();
    const metaLocale = useMetaLocale();

    const pageviewElement = ref<HTMLSpanElement>();
    const pageViews = ref(0);

    onMounted(() => {
      const observer = new MutationObserver(() => {
        const count = pageviewElement.value!.textContent;

        if (count && !isNaN(Number(count))) pageViews.value = Number(count);
      });

      observer.observe(pageviewElement.value!, {
        childList: true,
      });

      onUnmounted(() => {
        observer.disconnect();
      });
    });

    return (): VNode | null =>
      props.pageview
        ? h(
            "span",
            {
              class: "page-pageview-info",
              "aria-label": `${metaLocale.value.views}${
                props.pure ? "" : "üî¢"
              }`,
              ...(props.pure ? {} : { "data-balloon-pos": "down" }),
            },
            [
              h(pageViews.value < 1000 ? EyeIcon : FireIcon),
              h(
                "span",
                {
                  ref: pageviewElement,
                  class: "waline-pageview-count",
                  id: "ArtalkPV",
                  /** visitorID */
                  "data-path": isString(props.pageview)
                    ? props.pageview
                    : withBase(route.path),
                },
                "..."
              ),
            ]
          )
        : null;
  },
});
